.container-header {
    height: 70px;
    padding: 0px 10px;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.btn-nav {
    border-radius: 5px;
    border: 0;
    font-size: 1.2rem;
    padding: 10px 15px;
    cursor: pointer;
    font-weight: 400;
    margin-right: 10px;
}

.btn-primary {
    background-color: var(--colorPrimary);
    color: var(--colorContent);
}

.btn-primary:hover {
    background-color: var(--colorPrimaryHover);
}


.btn-secondary {
    background-color: var(--colorTertiary);
    color: var(--colorContentDark);
}

.btn-secondary:hover {
    background-color: var(--colorTertiaryHover);
}



.nav-link {
    color: var(--colorContentDarkSecondary);
    text-decoration: none;
    padding: 10px;
    font-size: 1.2rem;
    font-weight: 500;
    margin-right: 10px;
}

.nav-link:hover {
    text-decoration: underline;
}

.container-logo {
    font-size: 2rem;
    font-weight: 600;
    color: var(--colorContentDarkSecondary);
    cursor: pointer;
}

.txt-logo {
    color: var(--colorSecondary);
}

.btn-profile {
    display: flex;
    align-items: center;
    justify-content: center;
}

.btn-profile img {
    width: 2.7rem;
    height: 2.7rem;
    margin: 0 15px 0 15px;
    cursor: pointer;
}

@media(max-width: 700px) {
    .container-logo {
        margin-top: 4px;
    }
    .nav-link {
        display: none;
    }
    .btn-nav {
        font-size: 0.9rem;
    }
    .btn-secondary {
        display: none;
    }
}